$backgroundColor: rgb(255, 255, 255);
$selectBackgroundColor: rgb(107, 250, 236);
.hq-selector {
  display: flex;
  flex-direction: column;
  z-index: 1;
  margin: 15px 20px;
  padding: 5px;
  left: 0;
  text-align: left;
  background-color: rgba($backgroundColor, 0.9);
  border-radius: 5px;
  cursor: pointer;
  width: 150px;
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.5);
}

.hq-selector-header {
  display: flex;
  flex-direction: row;
  padding-left: 10px;
  align-items: center;
  justify-content: space-between;
  height: 30px;
}

.hq-selector-header-text,
.hq-selector-header-arrow {
  padding-right: 10px;
}
.hq-selector-selections {
  display: none;
}
.hq-selector:hover {
  .hq-selector-selections {
    display: inline-block;
  }
  .hq-selector-header-arrow {
    transform: rotate(90deg);
    transition: transform 0.3s ease;
  }
}
.hq-selector-selection {
  margin: 5px;
  padding: 10px;
  border-radius: 5px;
  &:hover {
    background-color: $selectBackgroundColor;
    transition: background-color 0.7s ease;
  }
}

.selected.hq-selector-selection {
  background-color: $selectBackgroundColor;
}
